import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  clients: [],
};

const clientsSlice = createSlice({
  name: 'clients',
  initialState,
  reducers: {
    //      bd
    addClientsRedux(state, { payload }) {
      state.clients = payload;
      // console.log(payload);
    },
    //   
    unshiftClientRedux(state, { payload }) {
      state.clients.unshift(payload);
      // console.log(payload);
    },

    //  
    deleteClientRedux(state, { payload }) {
      for (let index = 0; index < state.clients.length; index++) {
        const client = state.clients[index];

        if (payload === client.client_id) {
          state.clients.splice(index, 1);
          break;
        }
      }
    },
    //   
    pushParticipan(state, { payload }) {
      const client_id = payload.client_id;
      const participan_object = payload.participan_object;
      const groupObject_link = payload.groupObject_link;

      for (let index = 0; index < state.clients.length; index++) {
        const item = state.clients[index];
        if (item.client_id === client_id) {
          item.participationGroups[groupObject_link] = participan_object;
          break;
        }
      }
    },
    //   
    deleteParticipanRedux(state, { payload }) {
      const client_id = payload.client_id;
      const groupObject_link = payload.groupObject_link;

      for (let index = 0; index < state.clients.length; index++) {
        const item = state.clients[index];
        if (item.client_id === client_id) {
          delete item.participationGroups[groupObject_link];
          break;
        }
      }
    },
    // ,     
    accessToMenuRedux(state, { payload }) {
      const client_id = payload.client_id;
      const groupObject_link = payload.groupObject_link;
      const isAccess = payload.isAccess;
      // participationGroups
      state.clients.forEach((client) => {
        if (client.client_id === client_id) {
          client.participationGroups[groupObject_link].accessGroup = isAccess;
        }
      });
    },
    //  
    editsCustomerDataRedux(state, { payload }) {
      for (let index = 0; index < state.clients.length; index++) {
        const client = state.clients[index];

        if (client.client_id === payload.client_id) {
          state.clients.splice(index, 1, payload);
          break;
        }
      }
    },
    //
    editsClientNotesRedux(state, { payload }) {
      const client_id = payload.client_id;
      const notesText = payload.notesText;
      for (let index = 0; index < state.clients.length; index++) {
        const client = state.clients[index];

        if (client.client_id === client_id) {
          state.clients[index].notes = notesText;
          break;
        }
      }
    },
  },
});

export const {
  addClientsRedux,
  unshiftClientRedux,
  pushParticipan,
  deleteParticipanRedux,
  accessToMenuRedux,
  editsCustomerDataRedux,
  editsClientNotesRedux,
  deleteClientRedux,
} = clientsSlice.actions;

export default clientsSlice.reducer;
