import React, { useState } from 'react';
import styles from './MeasurementsAllClients.module.css';

export default function MeasurementsAllClients(clientsMeasurements, group_id) {
  //      
  const getGroupMeasurements = (measurements) => {
    const groupMeasurements = measurements.filter((item) => {
      return item.group_id === group_id;
    });
    return groupMeasurements;
  };
  //           
  const calculationAllClients = (clientsMeasurements) => {
    //    
    let calculationKg = { fullNames: [], count: 0, sumValue: 0, result: null };
    //    
    let calculationMeasurements = { fullNames: [], count: 0, sumValue: 0, result: null };

    clientsMeasurements.forEach((item, index) => {
      //      
      const measurements = getGroupMeasurements(item.measurements);

      if (measurements.length && measurements.length >= 4) {
        if (!Number.isNaN(measurements[0].kg - measurements[measurements.length - 1].kg)) {
          calculationKg.fullNames.push(clientsMeasurements[index].fullName);
          calculationKg.count++;
          calculationKg.sumValue += measurements[0].kg - measurements[measurements.length - 1].kg;
        }
        // -----------
        if (
          !Number.isNaN(
            measurements[0].totalVolume - measurements[measurements.length - 1].totalVolume,
          )
        ) {
          calculationMeasurements.fullNames.push(clientsMeasurements[index].fullName);
          calculationMeasurements.count++;
          calculationMeasurements.sumValue +=
            measurements[0].totalVolume - measurements[measurements.length - 1].totalVolume;
        }
        // -----------
      }
    });

    const calculationKgResult = calculationKg.sumValue / calculationKg.count;
    const calculationMeasurementsResult =
      calculationMeasurements.sumValue / calculationMeasurements.count;

    calculationKg.result = Number.isInteger(calculationKgResult)
      ? calculationKgResult
      : calculationKgResult.toFixed(1);

    calculationMeasurements.result = Number.isInteger(calculationMeasurementsResult)
      ? calculationMeasurementsResult
      : calculationMeasurementsResult.toFixed(1);

    calculationKg.sumValue = Number.isInteger(calculationKg.sumValue)
      ? calculationKg.sumValue
      : calculationKg.sumValue.toFixed(1);

    calculationMeasurements.sumValue = Number.isInteger(calculationMeasurements.sumValue)
      ? calculationMeasurements.sumValue
      : calculationMeasurements.sumValue.toFixed(1);

    return {
      calculationKg,
      calculationMeasurements,
    };
  };
  //         
  const renderReport = ({ calculationKg, calculationMeasurements }) => {
    return (
      <div className={styles.report_wrapper}>
        <h3>C    '.</h3>
        <br />
        <div>
          <h3> ""</h3>
          <div>
             : <span>{calculationKg.count} </span>
          </div>
          <div>
              :{' '}
            <span>
              {calculationKg.sumValue >= 0
                ? '-' + calculationKg.sumValue + ' '
                : calculationKg.sumValue !== 'NaN'
                ? '+' + Math.abs(calculationKg.sumValue) + ' '
                : '-'}
            </span>
          </div>
          <div>
               :{' '}
            <span>
              {calculationKg.result >= 0
                ? '-' + calculationKg.result + ' '
                : calculationKg.result !== 'NaN'
                ? '+' + Math.abs(calculationKg.result) + ' '
                : '-'}
            </span>
          </div>
        </div>
        <br />
        <div>
          <h3> "'"</h3>

          <div>
             : <span>{calculationMeasurements.count} </span>
          </div>
          <div>
              ':{' '}
            <span>
              {calculationMeasurements.sumValue >= 0
                ? '-' + calculationMeasurements.sumValue + ' '
                : calculationMeasurements.sumValue !== 'NaN'
                ? '+' + Math.abs(calculationMeasurements.sumValue) + ' '
                : '-'}
            </span>
          </div>
          <div>
             '  :{' '}
            <span>
              {calculationMeasurements.result >= 0
                ? '-' + calculationMeasurements.result + ' '
                : calculationMeasurements.result !== 'NaN'
                ? '+' + Math.abs(calculationMeasurements.result) + ' '
                : '-'}
            </span>
          </div>
        </div>
        <br />
        <div>
          <h3>  ?</h3>
          <p>
               4            
              .
          </p>
          <ol className={styles.ol_wrappe}>
            <li>       .</li>
            <li>    (  )</li>
            <li>
                    (   
              )
            </li>
          </ol>
        </div>
      </div>
    );
  };

  return <>{renderReport(calculationAllClients(clientsMeasurements))}</>;
}
