import React, { useState } from 'react';
import styles from './PopupEditClient.module.css';
import { editsCustomerDataBd } from '../../firebase';
import { useDispatch } from 'react-redux';
import LoaderPage from '../LoaderPage/LoaderPage';
import { editsCustomerDataRedux } from '../../store/slices/clientsSlice';

export default function PopupEditClient({ client, handleClick }) {
  const dispatch = useDispatch();
  const [name, setName] = useState(client.name);
  const [surname, setSurname] = useState(client.surname);
  const [tel, setTel] = useState(client.tel);
  const [loader, setLoader] = useState(false);

  //  
  const editsCustomerData = async (client, nameProps, surnameProps, telProps) => {
    setLoader(true);
    const name = nameProps.trim();
    const surname = surnameProps.trim();
    const tel = telProps.trim();

    const newClientData = {
      ...client,
      newClient: false,
      name: name,
      surname: surname,
      tel: tel,
      fullName: name + ' ' + surname,
    };

    const isEditsCustomerDataBd = await editsCustomerDataBd(newClientData);
    handleClick(false);
    if (isEditsCustomerDataBd) {
      dispatch(editsCustomerDataRedux(newClientData));
    }
    setLoader(false);
  };

  return (
    <>
      <div className={styles.popup_wrapper}>
        <div className={styles.popup_contnent_wrapper}>
          <button className={styles.popup_close_btn} onClick={() => handleClick(false)}>
            
          </button>
          
          <input
            value={name}
            className={styles.popup_input}
            type="text"
            onChange={(e) => setName(e.target.value)}
          />
          
          <input
            value={surname}
            className={styles.popup_input}
            type="text"
            onChange={(e) => setSurname(e.target.value)}
          />
          
          <input
            value={tel}
            className={styles.popup_input}
            type="tel"
            onChange={(e) => setTel(e.target.value)}
          />
          <div className={styles.popup_add_btn_wrapper}>
            <button
              className={styles.popup_add_btn}
              onClick={() => editsCustomerData(client, name, surname, tel)}>
              
            </button>
          </div>
        </div>
      </div>
      {loader && <LoaderPage />}
    </>
  );
}
