import React, { useEffect, useState } from 'react';
import { pathHome } from '../../path';
import styles from './clients-page.module.css';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import AddClients from '../../components/AddClients/AddClients';
import { deleteClientBd, getClientsDb } from '../../firebase';
import { addClientsRedux, deleteClientRedux } from '../../store/slices/clientsSlice';
import LoaderPage from '../../components/LoaderPage/LoaderPage';

export default function ClientsPage() {
  const dispatch = useDispatch();
  const [popupAddClients, setPopupAddClients] = useState(false);
  const [loaderPage, setLoaderPage] = useState(false);
  const clients = useSelector((state) => state.clients.clients);

  useEffect(() => {
    if (!clients.length) {
      setLoaderPage(true);
      const getClients = async () => {
        const clients = await getClientsDb();
        dispatch(addClientsRedux(clients));
        // console.log('clients ===', clients);
        setLoaderPage(false);
      };
      getClients();
    }
  }, []);

  const deleteClient = async (client_id) => {
    setLoaderPage(true);
    const isDeleteClient = window.confirm(' ?');
    if (isDeleteClient) {
      const ifDeleteClient = await deleteClientBd(client_id);
      if (ifDeleteClient) {
        dispatch(deleteClientRedux(client_id));
        alert(' .');
      } else {
        alert('   .');
      }
    }
    setLoaderPage(false);
  };

  const renderClients = (clients) => {
    return clients.map((item) => {
      return (
        <div key={item.client_id} className={styles.client_wrapper}>
          {item.newClient && <div className={styles.client_new}> !</div>}
          <div className={styles.top_block}>
            <Link to={`/${pathHome}/client-info/${item.client_id}`} className={styles.client_info}>
              {item.fullName}
            </Link>
            <div
              className={styles.delete_client_wrapper}
              onClick={() => deleteClient(item.client_id)}>
              <svg
                width="21"
                height="20"
                viewBox="0 0 21 20"
                fill="none"
                xmlns="http://www.w3.org/2000/svg">
                <path
                  d="M3 3H18V18C18 19.1046 17.1046 20 16 20H5C3.89543 20 3 19.1046 3 18V3Z"
                  fill="#FF5C5E"></path>
                <rect width="21" height="2" rx="1" fill="#FF5C5E"></rect>
              </svg>
            </div>
          </div>
        </div>
      );
    });
  };

  return (
    <>
      <div className={styles.header_wrapper}>
        <Link className={styles.header_link} to={`/${pathHome}/home`}>
          <svg
            width="12"
            height="18"
            viewBox="0 0 12 18"
            fill="none"
            xmlns="http://www.w3.org/2000/svg">
            <path d="M0 9L11.25 0.339745V17.6603L0 9Z" fill="white" />
          </svg>
          
        </Link>
        <div> {clients.length ? clients.length : '-'} </div>
        <svg
          onClick={() => setPopupAddClients(true)}
          className={styles.interaction_item}
          width="35"
          height="36"
          viewBox="0 0 35 36"
          fill="none"
          xmlns="http://www.w3.org/2000/svg">
          <rect y="2.60376" width="34.7273" height="33.0189" rx="10" fill="#A22CA7" />
          <path
            d="M16.2182 26.4091V11.4091H18.7636V26.4091H16.2182ZM9.99089 20.1818V17.6364H24.9909V20.1818H9.99089Z"
            fill="white"
          />
        </svg>
      </div>
      <div className={styles.content_wrapper}>
        {clients.length ? (
          renderClients(clients)
        ) : loaderPage ? (
          <div>...</div>
        ) : (
          <div>:)</div>
        )}
      </div>
      {popupAddClients && <AddClients setPopup={setPopupAddClients} />}
      {loaderPage && <LoaderPage />}
    </>
  );
}
