export const addMenuBd = async (group_id, menuObject, groupName) => {
  try {
    // ----------   -----------------
    const menuGrroupRef = doc(db, 'menu', group_id);
    const path = 'menu.' + menuObject.id;
    await updateDoc(menuGrroupRef, {
      [path]: menuObject,
    });
    // ---------- /  -----------------

    // ----------   ----------------
    const menuHistoryRef = doc(db, 'menuHistory', menuObject.menu_history_id);
    const menuHistoryInfo = {
      group_id: group_id,
      groupName: groupName,
      id: new Date().getTime(),
      date: new Date().toLocaleDateString(), //   27.07.2022 ', new Date().toLocaleDateString()
      time: new Date().toLocaleTimeString(), //   13:54:43', new Date().toLocaleTimeString()
      day: new Date().getDay(), //    new Date().getDay();
    };
    setDoc(menuHistoryRef, { [menuObject.id]: menuHistoryInfo }, { merge: true });

    // ---------- /  ----------------

    // alert('  .');
    return true;
  } catch {
    alert('   .');

    return false;
  }
};
