export const removeMenuBd = async (group_id, indexRenderMenuDey, menuDay) => {
  // ---------    ---------
  const menuRef = doc(db, 'menu', group_id);
  const menuPath = 'menu.' + indexRenderMenuDey;

  await updateDoc(menuRef, {
    [menuPath]: deleteField(),
  });
  // --------- /   ---------

  // ---------    ---------
  const menuHistory = await getHistoryMenuBd('menuHistory', menuDay.menu_history_id);

  if (Object.keys(menuHistory).length > 1) {
    const historyMenuRef = doc(db, 'menuHistory', menuDay.menu_history_id);
    const historyPath = menuDay.id;

    await updateDoc(historyMenuRef, {
      [historyPath]: deleteField(),
    });
  } else {
    await deleteDoc(doc(db, 'menuHistory', menuDay.menu_history_id));
  }
  // --------- /   -------------------
};

