//    
export const addClientBd = async (clientData) => {
  //  
  const isAddUser = await addUser(clientData);
  //  
  const isAddMeasurements = await addUserMeasurements(clientData);
  const isAddLoginWebCounter = await addLoginWebCounter(clientData);

  //      
  if (isAddUser && isAddMeasurements && isAddLoginWebCounter) {
    try {
      await setDoc(doc(db, 'clients', clientData.client_id), clientData);
      return true;
    } catch (e) {
      await deleteDoc(doc(db, 'users', clientData.client_id));
      await deleteDoc(doc(db, 'measurements', clientData.client_id));
      await deleteDoc(doc(db, 'loginWebCounter', clientData.client_id));
      alert('   .');
      return false;
    }
  } else {
    await deleteDoc(doc(db, 'users', clientData.client_id));
    await deleteDoc(doc(db, 'measurements', clientData.client_id));
    await deleteDoc(doc(db, 'loginWebCounter', clientData.client_id));
    alert('   .');
    return false;
  }
};

