export const deleteClientGroupBd = async (client_id, groupObject_link, group_id) => {
  const clientParticipationRef = doc(db, 'clients', client_id);
  const usersParticipationRef = doc(db, 'users', client_id);
  const loginWebCounterParticipationRef = doc(db, 'loginWebCounter', client_id);

  const path = 'participationGroups.' + groupObject_link;

  try {
    await updateDoc(clientParticipationRef, {
      [path]: deleteField(),
    });
    await updateDoc(usersParticipationRef, {
      [path]: deleteField(),
    });
    await updateDoc(loginWebCounterParticipationRef, {
      groupMember: arrayRemove(group_id),
    });
    return true;
  } catch {
    alert('     .');
    return false;
  }
};
