export const editsCustomerDataBd = async (client) => {
  try {
    const updateClient = {
      name: client.name,
      surname: client.surname,
      tel: client.tel,
      fullName: client.fullName,
    };
    await updateDoc(doc(db, 'clients', client.client_id), updateClient);

    const updateName = {
      name: client.name,
      surname: client.surname,
      fullName: client.fullName,
    };

    await updateDoc(doc(db, 'measurements', client.client_id), updateName);
    await updateDoc(doc(db, 'loginWebCounter', client.client_id), updateName);
    await updateDoc(doc(db, 'users', client.client_id), updateName);

    return true;
  } catch {
    alert('   .');
    return false;
  }
};
