export const getGroupName = (groups, group_id) => {
  for (let index = 0; index < groups.length; index++) {
    const group = groups[index];

    if (group.group_id === group_id) {
      return group.groupName;
    }
  }
};
export const getGroupForId = (groups, group_id) => {
  for (let index = 0; index < groups.length; index++) {
    const group = groups[index];

    if (group.group_id === group_id) {
      return group;
    }
  }
};
