import React, { useState } from 'react';
import styles from './AddClients.module.css';
import { addClientBd } from '../../firebase';
import LoaderPage from '../LoaderPage/LoaderPage';
import { useDispatch } from 'react-redux';
import { unshiftClientRedux } from '../../store/slices/clientsSlice';
import { generateRandomString } from '../../helper';

export default function AddClients({ setPopup }) {
  const dispatch = useDispatch();
  const [name, setName] = useState('');
  const [surname, setSurname] = useState('');
  const [tel, setTel] = useState('');
  const [loader, setLoader] = useState(false);

  const addClient = async (clientData) => {
    setLoader(true);
    const data = {
      name: clientData.name.trim(),
      surname: clientData.surname.trim(),
      fullName: clientData.name.trim() + ' ' + clientData.surname.trim(),
      tel: clientData.tel.trim(),
      client_id: generateRandomString(15),
      time_milliseconds: new Date().getTime(), //    1  1970 
      date: new Date().toLocaleDateString(), //  
      date_time: new Date().toLocaleTimeString(), //  
      date_day: new Date().getDay(), //  
      notes: '',
      participationGroups: {},
      accessMeasurements: {},
    };

    const isAddClientBd = await addClientBd(data);
    if (isAddClientBd) {
      dispatch(unshiftClientRedux({ ...data, newClient: true }));
      alert('    .');
    }
    setPopup(false);
    setLoader(false);
  };

  return (
    <>
      <div className={styles.popup_wrapper}>
        <div className={styles.popup_contnent_wrapper}>
          <button className={styles.popup_close_btn} onClick={() => setPopup(false)}>
            
          </button>
          
          <input
            className={styles.popup_input}
            type="text"
            value={name}
            onChange={(e) => setName(e.target.value)}
          />
          
          <input
            className={styles.popup_input}
            type="text"
            value={surname}
            onChange={(e) => setSurname(e.target.value)}
          />
          
          <input
            className={styles.popup_input}
            type="tel"
            value={tel}
            placeholder="380966054541"
            onChange={(e) => setTel(e.target.value)}
          />
          <div className={styles.popup_add_btn_wrapper}>
            <button
              className={styles.popup_add_btn}
              onClick={() => addClient({ name, surname, tel })}>
              
            </button>
          </div>
        </div>
      </div>
      {loader && <LoaderPage />}
    </>
  );
}
