import React from 'react';
import styles from './AddMeal.module.css';
import { useDispatch, useSelector } from 'react-redux';
import {
  addMealRedux,
  removeMealRedux,
  addMealItemRedux,
  addIngredientRedux,
  addTitleIngredientRedux,
  addSubIngredientRedux,
  removeIngredientsGropRedux,
  removeIngredientRedux,
  onChangeIngredientRedux,
} from '../../store/slices/menuSlice';

export default function AddMeal({ mealName }) {
  // console.log(mealName);
  const dispatch = useDispatch();
  const menuText = useSelector((state) => state.menu.menuDay[mealName].menuText);
  const mealTitle = useSelector((state) => state.menu.menuDay[mealName].title);
  const mealTitleBtn = useSelector((state) => state.menu.menuDay[mealName].titleBtn);

  //  
  const addMeal = (mealName) => {
    dispatch(addMealRedux(mealName));
  };
  //  
  const removeMeal = (mealName, menuIndex) => {
    let isRemove = window.confirm(` C ${menuIndex + 1} ?`);
    if (isRemove) {
      dispatch(removeMealRedux({ mealName, menuIndex }));
    }
  };
  //
  const addMealItem = (mealName, menuIndex, inputValue, itemName) => {
    dispatch(addMealItemRedux({ mealName, menuIndex, inputValue, itemName }));
  };
  //  
  const addIngredient = (mealName, menuIndex) => {
    dispatch(addIngredientRedux({ mealName, menuIndex }));
  };
  //    
  const addTitleIngredient = (mealName, menuIndex) => {
    dispatch(addTitleIngredientRedux({ mealName, menuIndex }));
  };
  //     
  const addSubIngredient = (mealName, menuIndex, indexIngredientGrop) => {
    dispatch(addSubIngredientRedux({ mealName, menuIndex, indexIngredientGrop }));
  };
  //   
  const removeIngredientsGrop = (mealName, menuIndex, indexIngredientGrop) => {
    // console.log('removeIngredientsGrop/');
    let isRemove = window.confirm('?');
    if (isRemove) {
      dispatch(removeIngredientsGropRedux({ mealName, menuIndex, indexIngredientGrop }));
    }
  };
  //  
  const removeIngredient = (mealName, menuIndex, indexIngredientGrop, indexIngredient) => {
    // console.log('removeIngredientsGrop/');
    let isRemove = window.confirm(' ?');
    if (isRemove) {
      dispatch(
        removeIngredientRedux({ mealName, menuIndex, indexIngredientGrop, indexIngredient }),
      );
    }
  };
  //   value  
  const onChangeInputIngredient = (
    mealName,
    menuIndex,
    indexIngredientGrop,
    indexIngredient,
    inputValue,
  ) => {
    // console.log('onChangeInputIngredient/inputValue ===', inputValue);
    dispatch(
      onChangeIngredientRedux({
        mealName,
        menuIndex,
        indexIngredientGrop,
        indexIngredient,
        inputValue,
      }),
    );
  };
  //===============================================================

  const renderIngredient = (
    mealName,
    menuIndex,
    indexIngredientGrop,
    ingredientsItems,
    titleIngredients,
  ) => {
    // console.log(ingredientsItems);
    return ingredientsItems.map((ingredient, indexIngredient) => {
      // breakfast.menuText[0].ingredients[0].ingredientsItems[0].text === text_1_0
      // mealName.menuText[menuIndex].ingredients[indexIngredientGrop].ingredientsItems[indexIngredient].text === text_1_0

      return (
        <div key={ingredient.id} className={styles.ingredient_wrapper}>
          {titleIngredients ? (
            <>
              <input
                type="text"
                className={styles.ingredient_input + ' ' + styles.ingredient_input_title}
                placeholder="..."
                defaultValue={ingredient.text}
                onChange={(e) =>
                  onChangeInputIngredient(
                    mealName,
                    menuIndex,
                    indexIngredientGrop,
                    indexIngredient,
                    e.target.value,
                  )
                }
              />
              <button
                className={styles.ingredient_btn_minus}
                onClick={() => removeIngredientsGrop(mealName, menuIndex, indexIngredientGrop)}>
                -
              </button>
            </>
          ) : indexIngredient === 0 ? (
            <>
              <input
                type="text"
                className={styles.ingredient_input + ' ' + styles.ingredient_input_main}
                placeholder=" ..."
                defaultValue={ingredient.text}
                onChange={(e) =>
                  onChangeInputIngredient(
                    mealName,
                    menuIndex,
                    indexIngredientGrop,
                    indexIngredient,
                    e.target.value,
                  )
                }
              />
              <div className={styles.ingredient_btn_wrapper}>
                <button
                  className={styles.ingredient_btn_plus_main}
                  onClick={() => addSubIngredient(mealName, menuIndex, indexIngredientGrop)}>
                  +
                </button>
                <button
                  className={styles.ingredient_btn_minus_main}
                  onClick={() => removeIngredientsGrop(mealName, menuIndex, indexIngredientGrop)}>
                  -
                </button>
              </div>
            </>
          ) : (
            <div className={styles.ingredient_replacement_wrapper}>
              <input
                type="text"
                className={styles.ingredient_input}
                placeholder=" ..."
                defaultValue={ingredient.text}
                onChange={(e) =>
                  onChangeInputIngredient(
                    mealName,
                    menuIndex,
                    indexIngredientGrop,
                    indexIngredient,
                    e.target.value,
                  )
                }
              />
              <button
                className={styles.ingredient_btn_minus}
                onClick={() =>
                  removeIngredient(mealName, menuIndex, indexIngredientGrop, indexIngredient)
                }>
                -
              </button>
            </div>
          )}
        </div>
      );
    });
  };

  const renderIngredients = (mealName, menuIndex, ingredients) => {
    // console.log(
    //   'renderIngredients/mealName, menuIndex, ingredients === ',
    //   mealName,
    //   menuIndex,
    //   ingredients,
    // );
    return ingredients.map((ingredientItem, indexIngredientGrop) => {
      // console.log('renderIngredients/ingredient === ', ingredientItem);
      return (
        <div key={ingredientItem.id}>
          {renderIngredient(
            mealName,
            menuIndex,
            indexIngredientGrop,
            ingredientItem.ingredientsItems,
            ingredientItem.titleIngredients,
          )}
        </div>
      );
    });
  };
  //  
  const renderMenu = (menuArray) => {
    // console.log('renderMenu/menuArray', menuArray);
    return menuArray.map((menuItem, menuIndex) => {
      return (
        <div key={menuItem.id} className={styles.meal_wrapper}>
          <h4> {menuIndex + 1}</h4>
          <input
            type="text"
            className={styles.input_meal_title}
            placeholder=" ..."
            defaultValue={menuItem.title}
            onChange={(e) => addMealItem(mealName, menuIndex, e.target.value, 'title')}
          />
          :
          <div className={styles.ingredients_wrapper}>
            {renderIngredients(mealName, menuIndex, menuItem.ingredients)}
          </div>
          <div className={styles.ingredient_main_btn_wrapper}>
            <button
              className={styles.ingredient_btn + ' ' + styles.plus_ingredient_btn}
              onClick={() => addIngredient(mealName, menuIndex)}>
              +
            </button>
            <button
              className={styles.ingredient_btn + ' ' + styles.plus_ingredient_title_btn}
              onClick={() => addTitleIngredient(mealName, menuIndex)}>
              +
            </button>
          </div>
          <textarea
            rows="5"
            className={styles.meal_text_textarea}
            placeholder="   ..."
            defaultValue={menuItem.text}
            onChange={(e) => addMealItem(mealName, menuIndex, e.target.value, 'text')}></textarea>
          <input
            type="text"
            className={styles.input_meal_title}
            placeholder="  ..."
            defaultValue={menuItem.videoUrl}
            onChange={(e) => addMealItem(mealName, menuIndex, e.target.value.trim(), 'videoUrl')}
          />
          <button
            className={styles.remove_meal_btn}
            onClick={() => removeMeal(mealName, menuIndex)}>
              
          </button>
        </div>
      );
    });
  };

  return (
    <div className={styles.item_wrapper}>
      <h2 className={styles.item_title}>{mealTitle}</h2>
      <div className={styles.meals_container}>
        <div className={styles.meals_wrapper}>{renderMenu(menuText)}</div>
        <button className={styles.add_meal_btn} onClick={() => addMeal(mealName)}>
          {mealTitleBtn}
        </button>
      </div>
    </div>
  );
}
