import React, { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import { pathHome } from '../../path';
import styles from './group-home-page.module.css';
import { useDispatch, useSelector } from 'react-redux';
import LoaderPage from '../../components/LoaderPage/LoaderPage';
import { addDocBd, getDocBd, getGrupListDb, toggleGroupAccessAllBD } from '../../firebase';
import { addGroupRedux } from '../../store/slices/groupsSlice';
import { getGroupForId, getGroupName } from '../../helperProject';

export default function GroupHomePage() {
  const dispatch = useDispatch();
  let { group_id } = useParams();

  const [loaderPage, setLoaderPage] = useState(false);
  const [viberGroupLink, setViberGroupLink] = useState('');
  const [telegramGroupLink, setTelegramGroupLink] = useState('');
  const [copyGroupIdTrue, setCopyGroupIdTrue] = useState('');
  const [accessGroupAll, setAccessGroupAll] = useState(false);

  const groups = useSelector((state) => state.groups.groupsList);

  const isActiveAccessAll = (groups, group_id) => {
    const group = getGroupForId(groups, group_id);

    if (group && group.groupAccessAll) {
      setAccessGroupAll(true);
    }
    if (group && !group.groupAccessAll) {
      setAccessGroupAll(false);
    }
  };

  useEffect(() => {
    setLoaderPage(true);
    const fetchData = async () => {
      if (!groups.length) {
        const groupsListArray = await getGrupListDb();
        dispatch(addGroupRedux(groupsListArray));
        isActiveAccessAll(groupsListArray, group_id);
      } else {
        isActiveAccessAll(groups, group_id);
      }
      const groupsLinks = await getDocBd('groupsLinks', group_id);

      if (groupsLinks) {
        setViberGroupLink(groupsLinks.viberGroupLink);
        setTelegramGroupLink(groupsLinks.telegramGroupLink);
      }

      setLoaderPage(false);
    };

    fetchData();
  }, []);

  const addLinksGroups = async (value) => {
    setLoaderPage(true);

    const ifAddLinksGroups = addDocBd('groupsLinks', group_id, value);

    if (ifAddLinksGroups) {
      alert(' .');
    } else {
      alert('!!!');
    }

    setLoaderPage(false);
  };

  const copyGroupId = (group_id) => {
    navigator.clipboard
      .writeText(group_id)
      .then(() => {
        setCopyGroupIdTrue(group_id);
        setTimeout(() => {
          setCopyGroupIdTrue(false);
        }, 700);
      })
      .catch((err) => {
        alert('  !');
        console.log(err);
      });
  };

  //      
  const accessAll = () => {
    const toggleGroupAccessAll = async (groupId, access) => {
      setLoaderPage(true);

      let isAccess = window.confirm('   ?');
      if (isAccess) {
        const isToggle = await toggleGroupAccessAllBD(groupId, access);
        if (isToggle) {
          setAccessGroupAll(access);
        } else {
          setLoaderPage(false);
          return;
        }
      }

      setLoaderPage(false);
    };

    if (accessGroupAll) {
      toggleGroupAccessAll('S1a7I3i2L6E209H967I8n8G1i6QM', false);
    }
    if (!accessGroupAll) {
      toggleGroupAccessAll('S1a7I3i2L6E209H967I8n8G1i6QM', true);
    }
  };

  return (
    <>
      <div className={styles.header_wrapper}>
        <Link className={styles.header_link} to={`/${pathHome}/home`}>
          <svg
            width="12"
            height="18"
            viewBox="0 0 12 18"
            fill="none"
            xmlns="http://www.w3.org/2000/svg">
            <path d="M0 9L11.25 0.339745V17.6603L0 9Z" fill="white" />
          </svg>
          
        </Link>
      </div>
      <div className={styles.content_wrapper}>
        {groups.length ? (
          <>
            <div className={styles.content_title}>{getGroupName(groups, group_id)}</div>

            <Link className={styles.btn_link} to={`/${pathHome}/menu/${group_id}`}>
              
            </Link>
            <Link className={styles.btn_link} to={`/${pathHome}/participants/${group_id}`}>
              
            </Link>
            {/* <Link className={styles.btn_link} to={`/${pathHome}/control/${group_id}`}>
              
            </Link> */}
            <Link className={styles.btn_link} to={`/${pathHome}/group-activity/${group_id}`}>
              
            </Link>
            <Link className={styles.btn_link} to={`/${pathHome}/statistics/${group_id}`}>
              
            </Link>

            <div className={styles.group_access_wrapper}>
              <div className={styles.group_id_wrapper}>
                <div>ID :</div>
                <div className={styles.group_id} onClick={() => copyGroupId(group_id)}>
                  {group_id}
                </div>
                {copyGroupIdTrue === group_id && (
                  <div className={styles.alertCopyTrue}>!</div>
                )}
              </div>
              <div className={styles.group_access_selected_wrapper}>
                <div>     </div>
                <div
                  onClick={() => accessAll()}
                  className={
                    accessGroupAll
                      ? styles.group_access_selected_btn +
                        ' ' +
                        styles.group_access_selected_btn_active
                      : styles.group_access_selected_btn
                  }></div>
              </div>
            </div>

            <div className={styles.addLinksGroups_wrapper}>
                 Viber
              <input
                className={styles.popup_input}
                type="text"
                value={viberGroupLink}
                onChange={(e) => setViberGroupLink(e.target.value)}
              />
                 Telegram
              <input
                className={styles.popup_input}
                type="text"
                value={telegramGroupLink}
                onChange={(e) => setTelegramGroupLink(e.target.value)}
              />
              <button
                className={styles.save_btn}
                onClick={() =>
                  addLinksGroups({
                    viberGroupLink: viberGroupLink.trim(),
                    telegramGroupLink: telegramGroupLink.trim(),
                  })
                }>
                
              </button>
            </div>
          </>
        ) : (
          <div>:)</div>
        )}
      </div>
      {loaderPage && <LoaderPage />}
    </>
  );
}
