import React, { useEffect, useState } from 'react';
import styles from './statistics-page.module.css';
import { useNavigate, useParams } from 'react-router-dom';
import PopupStatistics from '../../components/PopupStatistics/PopupStatistics';
import { useDispatch, useSelector } from 'react-redux';
import { addStatisticsRedux } from '../../store/slices/showPopupStatisticsSlice';
import { bodyScrollOverflowHidden } from '../../helper';
import { getMeasurementsDb } from '../../firebase';
import LoaderPage from '../../components/LoaderPage/LoaderPage';
import { addMeasurementsRedux } from '../../store/slices/measurementsSlice';
import MeasurementsAllClients from '../../components/MeasurementsAllClients/MeasurementsAllClients';

export default function StatisticsPage() {
  let { group_id } = useParams();
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [loaderPage, setLoaderPage] = useState(false);
  const [showResultGroup, setShowResultGroup] = useState(false);

  const clientsMeasurements = useSelector((state) => state.measurements.measurements);

  useEffect(() => {
    const getMeasurements = async () => {
      setLoaderPage(true);
      const measurements = await getMeasurementsDb();

      dispatch(addMeasurementsRedux(measurements));
      setLoaderPage(false);
    };
    getMeasurements();
  }, []);

  //      
  const getGroupMeasurements = (measurements) => {
    const groupMeasurements = measurements.filter((item) => {
      return item.group_id === group_id;
    });
    return groupMeasurements;
  };
  //       
  const calculationStatistics = (items) => {
    let calculationResult = [];

    if (items.length > 1) {
      for (let index = 0; index < items.length; index++) {
        if (items[index + 1]) {
          const kg =
            items[index].kg !== '' && items[index + 1].kg !== ''
              ? items[index].kg - items[index + 1].kg
              : '-';
          const totalVolume = items[index].totalVolume - items[index + 1].totalVolume;

          const itemObject = {
            id: items[index].id,
            kg: Number.isInteger(kg) ? kg : kg !== '-' ? kg.toFixed(1) : '-',
            totalVolume: Number.isInteger(totalVolume) ? totalVolume : totalVolume.toFixed(1),
          };

          //             
          calculationResult.push(itemObject);
        }
      }
    }

    return calculationResult;
  };

  //   
  const renderMeasurements = (items) => {
    return items.map((item) => {
      return (
        <div key={item.id} className={styles.measurements_wrapper}>
          <div
            className={
              item.kg >= 0
                ? styles.measurement_value
                : item.kg !== '-'
                ? styles.measurement_value + ' ' + styles.statistics_unsatisfactory
                : styles.measurement_value + ' ' + styles.statistics_nan
            }>
            {item.kg >= 0
              ? '-' + item.kg + ' '
              : item.kg !== '-'
              ? '+' + Math.abs(item.kg) + ' '
              : '-'}
          </div>
          <div
            className={
              item.totalVolume >= 0
                ? styles.measurement_value
                : item.totalVolume !== 'NaN'
                ? styles.measurement_value + ' ' + styles.statistics_unsatisfactory
                : styles.measurement_value + ' ' + styles.statistics_nan
            }>
            {item.totalVolume >= 0
              ? '-' + item.totalVolume + ' '
              : item.totalVolume !== 'NaN'
              ? '+' + Math.abs(item.totalVolume) + ' '
              : '-'}
          </div>
        </div>
      );
    });
  };

  //         
  const renderCalculationAllMeasurements = (items) => {
    const kg = items[0].kg - items[items.length - 1].kg;
    const measurements = items[0].totalVolume - items[items.length - 1].totalVolume;

    const kgValue =
      (kg !== 'NuN') & Number.isInteger(Number(kg.toFixed(1)))
        ? Number(kg.toFixed(1))
        : kg.toFixed(1);
    const measurementsValue =
      (measurements !== 'NuN') & Number.isInteger(Number(measurements.toFixed(1)))
        ? Number(measurements.toFixed(1))
        : measurements.toFixed(1);

    return (
      <div className={styles.statistics_conclusion_wrapper}>
        <div
          className={
            kgValue >= 0
              ? styles.statistics_conclusion_kg
              : kgValue !== 'NaN'
              ? styles.statistics_conclusion_kg + ' ' + styles.statistics_unsatisfactory
              : styles.statistics_conclusion_kg + ' ' + styles.statistics_nan
          }>
          {kgValue >= 0
            ? '-' + kgValue + ' '
            : kgValue !== 'NaN'
            ? '+' + Math.abs(kgValue) + ' '
            : '-'}
        </div>
        <div
          className={
            measurementsValue >= 0
              ? styles.statistics_conclusion_measurements
              : measurementsValue !== 'NaN'
              ? styles.statistics_conclusion_measurements + ' ' + styles.statistics_unsatisfactory
              : styles.statistics_conclusion_measurements + ' ' + styles.statistics_nan
          }>
          {measurementsValue >= 0
            ? '-' + measurementsValue + ' '
            : measurementsValue !== 'NaN'
            ? '+' + Math.abs(measurementsValue) + ' '
            : '-'}
        </div>
      </div>
    );
  };

  //   (   )
  const renderOneMeasurements = (items) => {
    return (
      <div className={styles.oneMeasurements_wrapper}>
        <div className={styles.oneMeasurements_item}> {items[0].kg} </div>
        <div className={styles.oneMeasurements_item}>' {items[0].totalVolume} </div>
      </div>
    );
  };

  const showStatistic = (item) => {
    // setShowPopup(true);
    if (item.measurements.length) {
      dispatch(addStatisticsRedux(item));
      bodyScrollOverflowHidden();
    }
  };

  //   
  const renderClient = (clientsMeasurements) => {
    let countClient = 0;
    return clientsMeasurements.map((item, index) => {
      //    
      const measurements = getGroupMeasurements(item.measurements);
      measurements.length && countClient++;
      return measurements.length ? (
        <div
          key={item.client_id}
          onClick={() => showStatistic(item)}
          className={
            item.measurements.length > 0
              ? styles.statistics_item + ' ' + styles.statistics_item_active
              : styles.statistics_item
          }>
          <div className={styles.statistics_client_name}>
            <span className={styles.statistics_client_name_number}>{countClient})</span>{' '}
            {item.fullName}
          </div>
          {measurements.length > 2 && renderCalculationAllMeasurements(measurements)}
          {measurements.length > 0 ? (
            measurements.length === 1 ? (
              renderOneMeasurements(measurements)
            ) : (
              renderMeasurements(calculationStatistics(measurements))
            )
          ) : (
            <div>:)</div>
          )}
        </div>
      ) : null;
    });
  };

  return (
    <>
      {/* -------------- Header ------------------------ */}
      <div className={styles.header_wrapper}>
        <div className={styles.header_back_btn} onClick={() => navigate(-1)}>
          <svg
            width="12"
            height="18"
            viewBox="0 0 12 18"
            fill="none"
            xmlns="http://www.w3.org/2000/svg">
            <path d="M0 8.99999L11.25 0.339737V17.6602L0 8.99999Z" fill="white" />
          </svg>
          
        </div>
        <div className={styles.header_title}></div>
      </div>
      {/* -------------- /Header ------------------------ */}

      {loaderPage ? (
        <LoaderPage />
      ) : (
        <>
          {/* {console.log('clientsMeasurements ====', clientsMeasurements)} */}
          <div className={styles.content_wrapper}>{renderClient(clientsMeasurements)}</div>

          {/* ------  C        */}
          <button
            className={styles.calculate_result_btn}
            onClick={() => setShowResultGroup(!showResultGroup)}>
            {showResultGroup ? '' : '  '}
          </button>
          {showResultGroup && MeasurementsAllClients(clientsMeasurements, group_id)}
          {/* ------ / C         */}

          <PopupStatistics />
        </>
      )}
    </>
  );
}
