import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  groupsList: [
    // {
    //   groupName: '  13.10.224  5.11.2024',
    //   group_id: 'group_id_1',
    //   time_milliseconds: 12324412412414,
    // },
  ],
};

const groupsSlice = createSlice({
  name: 'groupsList',
  initialState,
  reducers: {
    //   
    addGroupRedux(state, { payload }) {
      state.groupsList = payload;
    },
    pushGroupRedux(state, { payload }) {
      state.groupsList.unshift(payload);
    },
  },
});

export const { addGroupRedux, pushGroupRedux } = groupsSlice.actions;

export default groupsSlice.reducer;
