import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  menuDay: {},
};

const menuSlice = createSlice({
  name: 'menu',
  initialState,
  reducers: {
    //     (    =  )
    addMenuDayDefaultRedux(state) {
      const menuDayDefault = {
        id: 'menu_day_' + new Date().getTime(),
        menu_history_id: 'menu_history_id_' + new Date().getTime(),
        dayNumber: 0, //    
        // countWater: 0, //  
        // sleep: '', //  
        // physicalActivity: '', //  
        // timeMilliseconds: new Date().getTime(), //,   1  1970: ', new Date().getTime());
        // date: new Date().toLocaleDateString(), //   27.07.2022 ', new Date().toLocaleDateString()
        // time: new Date().toLocaleTimeString(), //   13:54:43', new Date().toLocaleTimeString()
        // day: new Date().getDay(), //    new Date().getDay();
        breakfast: {
          id: 'breakfast_' + new Date().getTime() + 1,
          mealName: 'breakfast',
          mealTime: '',
          title: '',
          titleBtn: '+  ',
          menuView: 0,
          menuText: [],
        },
        snack: {
          id: 'snack_' + new Date().getTime() + 2,
          mealName: 'snack',
          mealTime: '',
          title: '',
          titleBtn: '+  ',
          menuView: 0,
          menuText: [],
        },
        dinner: {
          id: 'dinner_' + new Date().getTime() + 3,
          mealName: 'dinner',
          mealTime: '',
          title: '',
          titleBtn: '+  ',
          menuView: 0,
          menuText: [],
        },
        supper: {
          id: 'supper_' + new Date().getTime() + 4,
          mealName: 'supper',
          mealTime: '',
          title: '',
          titleBtn: '+  ',
          menuView: 0,
          menuText: [],
        },
      };
      state.menuDay = menuDayDefault;
    },
    //       
    addMenuEditRedux(state, { payload }) {
      state.menuDay = payload;
    },
    //   
    resetMenuDay(state) {
      state.menuDay = {};
    },
    //   
    addNumberDayRedux(state, { payload }) {
      state.menuDay.dayNumber = payload;
    },
    //  
    addMealRedux(state, { payload }) {
      // console.log('payload ==== ', payload);

      const mealName = payload;
      const item = {
        id: new Date().getTime(),
        title: '',
        videoUrl: '',
        ingredients: [],
        text: '',
      };

      state.menuDay[mealName].menuText = [...state.menuDay[mealName].menuText, item];
    },
    //  
    removeMealRedux(state, { payload }) {
      // console.log('removeMealRedux/payload ==== ', payload);
      const mealName = payload.mealName;
      const menuIndex = payload.menuIndex;
      state.menuDay[mealName].menuText.splice(menuIndex, 1);
    },
    //   
    addMealItemRedux(state, { payload }) {
      // console.log('addMealTitleRedux/payload === ', payload);
      const mealName = payload.mealName;
      const menuIndex = payload.menuIndex;
      const inputValue = payload.inputValue;
      const itemName = payload.itemName;

      state.menuDay[mealName].menuText[menuIndex][itemName] = inputValue;
    },
    //  
    addIngredientRedux(state, { payload }) {
      const mealName = payload.mealName;
      const menuIndex = payload.menuIndex;
      const date = new Date();
      const ingredient = {
        id: date.getTime(),
        itemActive: 0,
        ingredientsItems: [{ id: 1 + date.getTime(), text: '' }],
      };
      state.menuDay[mealName].menuText[menuIndex].ingredients = [
        ...state.menuDay[mealName].menuText[menuIndex].ingredients,
        ingredient,
      ];
    },
    //     
    addSubIngredientRedux(state, { payload }) {
      const mealName = payload.mealName;
      const menuIndex = payload.menuIndex;
      const indexIngredientGrop = payload.indexIngredientGrop;
      const subIngredient = { id: new Date().getTime(), text: '' };
      // state.menuDay[?].menuText[?].ingredients[?].ingredientsItems
      state.menuDay[mealName].menuText[menuIndex].ingredients[
        indexIngredientGrop
      ].ingredientsItems = [
        ...state.menuDay[mealName].menuText[menuIndex].ingredients[indexIngredientGrop]
          .ingredientsItems,
        subIngredient,
      ];
    },
    //    
    addTitleIngredientRedux(state, { payload }) {
      const mealName = payload.mealName;
      const menuIndex = payload.menuIndex;
      const date = new Date();
      const ingredient = {
        id: date.getTime(),
        itemActive: 0,
        titleIngredients: true,
        ingredientsItems: [{ id: 1 + date.getTime(), text: '' }],
      };
      state.menuDay[mealName].menuText[menuIndex].ingredients = [
        ...state.menuDay[mealName].menuText[menuIndex].ingredients,
        ingredient,
      ];
    },
    //   
    removeIngredientsGropRedux(state, { payload }) {
      const mealName = payload.mealName;
      const menuIndex = payload.menuIndex;
      const indexIngredientGrop = payload.indexIngredientGrop;
      // console.log('removeIngredientsGropRedux/payload === ', payload);
      // state.menuDay[?].menuText[?].ingredients[?].ingredientsItems = []
      state.menuDay[mealName].menuText[menuIndex].ingredients.splice(indexIngredientGrop, 1);
    },
    //  
    removeIngredientRedux(state, { payload }) {
      const mealName = payload.mealName;
      const menuIndex = payload.menuIndex;
      const indexIngredientGrop = payload.indexIngredientGrop;
      const indexIngredient = payload.indexIngredient;
      // console.log('removeIngredientsGropRedux/payload === ', payload);
      // state.menuDay[?].menuText[?].ingredients[?].ingredientsItems.splice(?, 1);
      state.menuDay[mealName].menuText[menuIndex].ingredients[
        indexIngredientGrop
      ].ingredientsItems.splice(indexIngredient, 1);
    },
    //    
    onChangeIngredientRedux(state, { payload }) {
      // console.log('onChangeIngredient/payload === ', payload);
      const mealName = payload.mealName;
      const menuIndex = payload.menuIndex;
      const indexIngredientGrop = payload.indexIngredientGrop;
      const indexIngredient = payload.indexIngredient;
      const inputValue = payload.inputValue;
      // state.menuDay[?].menuText[?].ingredients[?].ingredientsItems[?].text === ?
      state.menuDay[mealName].menuText[menuIndex].ingredients[indexIngredientGrop].ingredientsItems[
        indexIngredient
      ].text = inputValue;
    },
  },
});

export const {
  addMenuDayDefaultRedux,
  addMenuEditRedux,
  resetMenuDay,
  addNumberDayRedux,
  addMealRedux,
  removeMealRedux,
  addMealItemRedux,
  onChangeIngredientRedux,
  removeIngredientsGropRedux,
  removeIngredientRedux,
  addIngredientRedux,
  addTitleIngredientRedux,
  addSubIngredientRedux,
} = menuSlice.actions;

export default menuSlice.reducer;
